<template>
<div class="all">
  <el-tabs type="border-card" tab-position="left" style="height:800px" v-model="activeTab">
      <!--  -->
  <el-tab-pane name="first">
    <div slot="label" class="tabtag"><i class="el-icon-goods"></i> &nbsp;&nbsp;</div>
    <h2></h2>
    <div style="overflow:auto;height:730px">
    <div class="cart_box" v-for="(item,index) in cart_list" :key="index">
      <!--  -->
      <el-checkbox v-model="selectItem" :label="index" style="position:relative;top:-80px" @change="handleCheckedCitiesChange"></el-checkbox>
      <!--  -->
      <div class="cart_box1" ><img :src="item.film.cover" /></div>
      <!--  -->
       <div class="cart_box2">
         <h3 style="padding:0px">{{item.film.name}}</h3>
         <h5 style="padding:0px;margin-top:-5px">{{item.cart.seats}}</h5>
         <h5>{{item.cart.phone}}</h5>
         <h5>{{item.arr.date}}&nbsp;{{item.arr.startTime}}</h5>
       </div>
       <!--  -->
<div class="cart_box3" >:{{item.cart.price}}</div>
<div class="cart_box4" ><el-button type="danger" style="width:130px" @click="ClickDelete(item,index)"></el-button></div>
    </div>
    <!--  -->
    <div class="cart_footer">
       <div class="cart_footer_1" ><el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox></div>
       <div class="cart_footer_2"  ><el-button type="primary" style="width:130px" @click="cilckconfirmPay()"></el-button></div>
       <div class="cart_footer_3" >:{{totalprice}}</div>
    </div></div>
  </el-tab-pane>
  <!--  -->
<el-dialog
  title=""
  :visible.sync="confirmDelete"
  width="30%"
  :before-close="handleClose">
  <span>?</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="confirmDelete = false"> </el-button>
    <el-button type="primary" @click="HandleDeleteCartItem(item,index)"></el-button>
  </span>
</el-dialog>
<!-- 1 -->
<el-dialog
  title=""
  :visible.sync="confirmPay"
  width="30%"
  :before-close="handleClose">
  <span>:{{totalprice}}</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="confirmPay = false"> </el-button>
    <el-button type="primary" @click="confirmPay = false;confirmPay2 =true"></el-button>
  </span>
</el-dialog>

<!-- 2 -->
<el-dialog
  title=""
  :visible.sync="confirmPay2"
  width="30%"
  :before-close="handleClose">
  <img style="width:300px;height:300px;margin:10px 100px" src="../assets/img/QRcode.jpg" />
  <span slot="footer" class="dialog-footer">
    <el-button @click="confirmPay2 = false"> </el-button>
    <el-button type="primary" @click="SuccessPay()"></el-button>
    <el-button type="primary" @click="FailPay()"></el-button>
  </span>
</el-dialog>

<!--  -->


<!--  -->
  <el-tab-pane name="second">
      <span slot="label" class="tabtag"><i class="el-icon-tickets"></i></span>
      <h2></h2>
      <div style="overflow:auto;height:730px">
      <div class="my_ticket" v-for="(item,index) in ticket_state" :key="index">
        <div class="cart_box1" ><img :src="item.film.cover" /></div>
      <!--  -->
       <div class="cart_box2">
         <h3 style="padding:0px">{{item.film.name}}</h3>
         <h5 style="padding:0px;margin-top:-5px">{{item.order.seats}}</h5>
         <h5>:{{item.arr.date}}&nbsp;{{item.arr.startTime}}</h5>
         <h5>:{{item.arr.date}}&nbsp;{{item.arr.endTime}}</h5>
       </div>
       <!--  -->
<div class="cart_box3" >:{{item.order.price}}</div>
<div class="cart_box4" ><span class="ticket_state_css" :style="changeStateCSS(item)" >{{changeState(item)}}</span></div>
      </div>

      </div>



      </el-tab-pane>

<!--  -->
  <el-tab-pane name="third">
      <span slot="label" class="tabtag"><i class="el-icon-setting"></i></span>
       <h2></h2>
       <div class="set">
       <div class="basic_aside">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item>
          <p style="display:inline-block"></p>
          <el-input v-model="form.name" style="display:inline-block"></el-input>
        </el-form-item>
        <el-form-item prop="pass">
          <p></p>
          <el-input
            type="password"
            v-model="ruleForm.pass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <p></p>
          <el-radio-group v-model="form.sex">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <p></p>
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="form.date1"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
        </el-form-item>
         <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" >
        <el-form-item
          prop="email"
          :rules="[
            { required: true, message: '', trigger: 'blur' },
            {
              type: 'email',
              message: '',
              trigger: ['blur', 'change'],
            },
          ]"
        >
          <p style="display:block;position:relative;left:80px"></p>
          <el-input style="display:block;position:relative;left:80px" v-model="dynamicValidateForm.email"></el-input>
        </el-form-item>
        </el-form>
        <el-form-item>
          <p></p>
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit2()"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </div>
      <div class="picture">
      <div><el-upload
						class="avatar-uploader"
            action="http://47.115.220.25:8081/api/user/upload"
						:show-file-list="false"
						:on-success="handleAvatarSuccess"
						:before-upload="beforeAvatarUpload">
						<img v-if="imageUrl" :src="imageUrl" class="avatar">
						<i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload></div>
      <!-- <el-button type="primary" class="btn"></el-button> -->
      <p>jpg/png</p>
      <p></p>
    </div>
       </div>
      </el-tab-pane>
</el-tabs>
</div>
</template>

<script>
import{ModifySelf} from "@/api/user"
import{QueryCart,DeleteCart} from "@/api/cart";
import{QueryOrderByUid,SimPaySuccess,SimPayFail} from "@/api/order";
import { parseTime } from "@/plugins/DateTran";//
export default {
     data(){
       return{
         //
        imageUrl:'',
        dynamicValidateForm:{
        domains: [
          {
            value: "",
          },
        ],
        email: "", //
      },
      ruleForm: { pass: "" }, //
      form: {
        name: "", //
        region: "",
        date1: "",//
        date2: "",
        delivery: false,
        type: [],
        sex: "",// 
        desc: "", //
      },
			fileName:'',

         //
         item:{},
         index:99,
        //  Tab
        activeTab:this.$store.state.activeTab,
        //  
         selectItem:[],
         //
         confirmDelete:false,
         //
         confirmPay:false,
         confirmPay2:false,
         checkAll: false,
         isIndeterminate: true,
         //
        cart_list:[],
        //
        ticket_state:[
          {img:require("../assets/filmImg/error.gif"),film_name:"",seat:30,start_time:"2022/3/27 12:00",end_time:"2022/3/27 12:00",state:0,price:30},
          {img:require("../assets/filmImg/error.gif"),film_name:"",seat:30,start_time:"2022/3/27 12:00",end_time:"2022/3/27 12:00",state:1,price:10},
          {img:require("../assets/filmImg/error.gif"),film_name:"",seat:30,start_time:"2022/3/27 12:00",end_time:"2022/3/27 12:00",state:2,price:50},
          {img:require("../assets/filmImg/error.gif"),film_name:"",seat:30,start_time:"2022/3/27 12:00",end_time:"2022/3/27 12:00",state:3,price:50}
        ],
        //
        state0:[{color:"orange"},{state_desc:""}],
        state1:[{color:"red"},{state_desc:""}],
        state2:[{color:"green"},{state_desc:""}],
        state3:[{color:"blue"},{state_desc:""}],

       }
     },
     computed:{
       totalprice:function(){
         var total_price=0;
         for(var i=0;i<this.selectItem.length;i++)
         {total_price=total_price+this.cart_list[this.selectItem[i]].cart.price}
         return total_price;
       }
     },
     methods:{
       //
       beforeAvatarUpload(file) {
			const isJPG = file.type === 'image/jpeg';
			const isLt2M = file.size / 1024 / 1024 < 2;

			if (!isJPG) {
				this.$message.error(' JPG !');
			}
			if (!isLt2M) {
				this.$message.error(' 2MB!');
			}
			return isJPG && isLt2M;
		},
		handleAvatarSuccess(res, file) {
			console.log(res.data.fileName);
			this.fileName=res.data.fileName;
			this.imageUrl = URL.createObjectURL(file.raw);
		
		},
    onSubmit2() {
			let data={
				password:this.ruleForm.pass,
				avatar:this.fileName,
				nickname:this.form.name,
				email:this.dynamicValidateForm.email,
				birthday:this.form.date1,
				gender:this.form.sex,
				info:this.form.desc,
				id:localStorage.getItem("uid"),
			};
			console.log(data);
			
			ModifySelf(data).then((res)=>{
				if(res.code==200)
        {
          if(res.data.success=="success")
          {
            this.$message({message:res.data.msg,type:res.data.success})
            window.location.reload();
          }
          else{
            this.$message.error("")
          }
        }
        else{
          this.$message.error("")
        }
			});
    },
       //
       ClickDelete(item,index)
       {
         this.item=item;
         this.index=index;
         this.confirmDelete=true;
       },
       onSubmit() {
        console.log('submit!')
     },
     //
     SuccessPay(){
       for(var i=0;i<this.selectItem.length;i++)
       {
         const vo=this.cart_list[i];
        SimPaySuccess(vo).then((res)=>{
          if(res.code==200)
          {
            if(res.data.success=="success"){
                    this.$message({message:res.data.msg,type:"success"},)
                   }
                    else{
                 this.$message({message:res.data.msg,type:"warning"},)
                     }
          }
          else{
            this.$message.error("")
          }
          window.location.reload();
        })
        console.log(vo);
       }
       this.confirmPay2 = false;
     },
     //
     FailPay(){
      for(var i=0;i<this.selectItem.length;i++)
       {
         const vo=this.cart_list[i];
        SimPayFail(vo).then((res)=>{
          if(res.code==200)
          {
            if(res.data.success=="success"){
                    this.$message({message:res.data.msg,type:"success"},)
                   }
                    else{
                 this.$message({message:res.data.msg,type:"warning"},)
                     }
          }
          else{
            this.$message.error("")
          }
          window.location.reload();
        })
        console.log(vo);
       }
       this.confirmPay2 = false;
     },
     //
     cilckconfirmPay(){
       if(this.totalprice!=0)
       {
       this.confirmPay =true;
       }
       else{
         this.$message({message:"",type:"warning"});
       }

     },
     //
     HandleDeleteCartItem(item,index){
        DeleteCart(item.cart).then((res)=>{
          if(res.code==200){
            this.cart_list.splice(index,1);
          }
          else{
            this.$message.error("");
          }
        })
        window.location.reload();
     },
     //
     handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      //
      changeStateCSS(item){
        if(item.order.status==0){return this.state0[0]}
        if(item.order.status==1){return this.state1[0]}
        if(item.order.status==2){return this.state2[0]}
        if(item.order.status==3){return this.state3[0]}
      },
      changeState(item){
        if(item.order.status==0){return this.state0[1].state_desc}
        if(item.order.status==1){return this.state1[1].state_desc}
        if(item.order.status==2){return this.state2[1].state_desc}
        if(item.order.status==3){return this.state3[1].state_desc}
      },
      // 
      handleCheckAllChange(val) {
        if(this.checkAll==true)
        {this.selectItem=undefined;this.selectItem=new Array();
         for(var i=0;i<this.cart_list.length;i++){this.selectItem.push(i)}
        }
        if(this.checkAll==false)
        {this.selectItem=undefined;this.selectItem=new Array();}
        this.isIndeterminate = false;
      },
       // 
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.selectItem.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.selectItem.length;
        if(this.selectItem.length==this.cart_list.length){this.checkAll=true}
      },
      //,Cart_List
    Add_Cart_List(){
      QueryCart(   parseInt( localStorage.getItem("uid") )  ).then((res)=>{
        if(res.code==200)
        {
          console.log("");
         console.log(res);
         this.cart_list=res.data;
         for(var i=0;i<this.cart_list.length;i++)
         {
         
           this.cart_list[i].arr.date=parseTime(new Date(this.cart_list[i].arr.date),"{y}-{m}-{d}");
        
         }
        }
      })
  },
      // ticket_state
     AddtoOrderList(){
       QueryOrderByUid(parseInt( localStorage.getItem("uid") )).then((res)=>{
         if(res.code==200)
         {
           console.log("");
           this.ticket_state=res.data;
          
           for(var i=0;i<this.ticket_state.length;i++)
         {
         
           this.ticket_state[i].arr.date=parseTime(new Date(this.ticket_state[i].arr.date),"{y}-{m}-{d}");
        
         }
         }
       })
     },
  
     },
     
     beforeCreate:function(){
			console.log("BeforeCreate");
            this.$store.state.navnum=0;
            this.$store.state.color3.color="#5b5d63";
            this.$store.state.color1.color="#5b5d63";this.$store.state.color2.color="#5b5d63";this.$store.state.color4.color="#5b5d63";
		},
     created(){
    console.log("QueryCart,,cart_list,Cart_list");
      this.cart_list=undefined;
      this.cart_list=new Array();
      this.ticket_state=undefined;
      this.ticket_state=new Array();
      this.Add_Cart_List();
      this.AddtoOrderList();
  },
     
     
     }

</script>

<style lang="scss" scoped>
.all{width:1400px;overflow:auto;margin:20px auto}
.tabtag{display:block;width:200px;text-align:center;font-size:20px}
/*  */
.cart_box{width:1100px;height:180px;margin-top:30px;overflow:hidden;border: 1px solid rgb(187, 187, 187);padding-left: 5px;}
.cart_box1 img{width:140px;height:160px}
.cart_box1{display:inline-block;width:140px;height:160px;margin:10px 40px;overflow:hidden}
.cart_box2{display:inline-block;width:440px;height:160px;margin:10px 10px;overflow:hidden}
.cart_box3{display:inline-block;width:140px;height:160px;line-height:160px;margin:10px 30px;overflow:hidden}
.cart_box4{display:inline-block;width:140px;height:60px;line-height:60px;margin:60px 0px;overflow:hidden}
.cart_footer{width:1100px;height:60px;margin-top:30px;overflow:hidden}
.cart_footer_1{display:inline-block;height:60px;line-height:60px}
.cart_footer_2{display:inline-block;float:right;height:60px;line-height:60px;margin-right:30px}
.cart_footer_3{display:inline-block;height:60px;line-height:60px;float:right;width:180px}
/*  */
.my_ticket{
  width:1100px;height:180px;margin-top:30px;overflow:hidden;border: 1px solid rgb(187, 187, 187);
}
.ticket_state_css{width:130px;text-align:center;display:inline-block}
/*  */
.my_setting{
  width:1100px;height:700px;margin-top:30px;overflow:hidden;
}
