<template>
  <div class="outbox">
    <!--  -->
      <div class="header">
        <el-row>
          <el-col :span="6" class="header-item" >ID</el-col>
          <el-col :span="4" class="header-item" style="padding-left:15px" ></el-col>
          <el-col :span="3" class="header-item" style="padding-left:40px"></el-col>
          <el-col :span="11" class="header-item" style="padding-left:170px"></el-col>
        </el-row>
      </div>
    <!-- --->
      <!-- <div class="order-box" :key="index" v-for="(item,index) in testlist">
        <el-row>
          <el-col span="8" class="order-box-item1" >{{item.id}}</el-col>
          <el-col span="4" class="order-box-item1" >{{item.money}}</el-col>
          <el-col span="6" class="order-box-item2" ><div :class="changeOrderCss(item)">{{item.statue}}</div></el-col>
          <el-col span="6" class="order-box-item3" ><el-button class="wait1" @click="Operation_CancleOrder(index)"></el-button> &nbsp; <el-button class="fail1" @click="Operation_FailOrder(index)"></el-button></el-col>
        </el-row>
      </div> -->
  <!--  -->
  <el-collapse v-model="activeNames">
  <el-collapse-item  :name="index" disabled :key="index" v-for="(item,index) in order_list" style="border-bottom:1px solid black;">
    <template slot="title">
    <el-row style="background-color:white;width: 1800px;height: 48px;">
         <el-col :span="7" class="order-box-item1">{{item.order.id}}</el-col>
         <el-col :span="5" class="order-box-item1">{{item.order.price}}</el-col>
         <el-col :span="3" class="order-box-item2"><div :class="changeOrderCss(item)">{{item.order.status}}</div></el-col>
         <el-col :span="6" class="order-box-item3">
           <el-button class="wait1" @click="Operation_CancleOrder(index)"></el-button>
           <el-button class="fail1" @click="Operation_FailOrder(index)"></el-button>
           <el-button class="PadButton" @click="changePad(index)"></el-button>
         </el-col>
        </el-row>
        <!-- , -->
    </template>
    <div class="collapse-panel-item">ID:{{item.order.id}}</div>
    <div class="collapse-panel-item">ID:{{item.order.uid}}</div>
    <div class="collapse-panel-item">ID:{{item.arr.fid}}</div>
    <div class="collapse-panel-item">ID:{{item.order.aid}}</div>
    <div class="collapse-panel-item">:{{item.arr.name}}</div>
    <div class="collapse-panel-item">:{{item.order.seats}}</div>
    <div class="collapse-panel-item">:{{item.order.price}}</div>
    <div class="collapse-panel-item">:{{item.order.createTime}}</div>
    <div class="collapse-panel-item">:{{item.order.payAt}}</div>
  </el-collapse-item>
</el-collapse>
<!--  -->
<el-dialog title="" :visible.sync="dialogFormVisible">
  <el-form :model="form">
    <el-form-item label="" :label-width="formLabelWidth">
      <el-input v-model="form.name" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" :label-width="formLabelWidth">
       <el-input v-model="form.reason" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false"> </el-button>
    <el-button type="primary" @click="Operation_FailOrder_confirm(form_index)"> </el-button>
  </div>
</el-dialog>
<!--  -->
<el-dialog title="" :visible.sync="dialogCancleVisible">
  
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogCancleVisible = false"> </el-button>
    <el-button type="primary" @click="Operation_CancleOrder_confirm(form_index)"> </el-button>
  </div>
</el-dialog>
<!--  -->
  </div>
</template>

<script>
import { QueryAdminAccount,QueryAdminInfo } from "@/api/admin";
import { QueryAllorder ,CancleOneOrder,PostOeOrder} from "@/api/order";//
import { parseTime } from "@/plugins/DateTran";//
export default {
  data(){
    return{
      //
      dialogCancleVisible: false,
      // 
       form_index:-1,
       formLabelWidth: '90px',
       dialogFormVisible: false,
        form: {
          name: '',
          reason:'',
        },
        //
        // order_detail:[
        //   {
        //     order_id:"123",
        //     user_id:"12321321",
        //     movie_id:"ji33232",
        //     arrn_id:"ucbbr1",
        //     movie_name:"",
        //     seat_no:"14",
        //     order_cost:232,
        //     order_time:"12:00",
        //     pay_time:"13:00",
        //   },
        //   {
        //     order_id:"123",
        //     user_id:"12321321",
        //     movie_id:"ji33232",
        //     arrn_id:"ucbbr1",
        //     movie_name:"",
        //     seat_no:"14",
        //     order_cost:232,
        //     order_time:"12:00",
        //     pay_time:"13:00",
        //   },
        //   {
        //     order_id:"123",
        //     user_id:"12321321",
        //     movie_id:"ji33232",
        //     arrn_id:"ucbbr1",
        //     movie_name:"",
        //     seat_no:"14",
        //     order_cost:232,
        //     order_time:"12:00",
        //     pay_time:"13:00",
        //   },
        // ],
      // 
        activeNames:[],
        //
        testlist:[
          {id:"1343-2341-2323-9212",
           money:40,
           statue:"",
           order_id:"123",
            user_id:"12321321",
            movie_id:"ji33232",
            arrn_id:"ucbbr1",
            movie_name:"",
            seat_no:"14",
            order_cost:232,
            order_time:"12:00",
            pay_time:"13:00",
           },
          {id:"2332-2321-2122-8743",
           money:60,
           statue:"",
           order_id:"123",
            user_id:"12321321",
            movie_id:"ji33232",
            arrn_id:"ucbbr1",
            movie_name:"",
            seat_no:"14",
            order_cost:232,
            order_time:"12:00",
            pay_time:"13:00",
           },
           {id:"7632-2181-9522-8000",
           money:70,
           statue:"",
           order_id:"123",
            user_id:"12321321",
            movie_id:"ji33232",
            arrn_id:"ucbbr1",
            movie_name:"",
            seat_no:"14",
            order_cost:232,
            order_time:"12:00",
            pay_time:"13:00",
           },
        ],
         //*********()************
        order_list_origin:{},
        //*********************
        order_list:{},
        //
           cancled:{cancle:true,pay:false,wait:false,fail:false},
           paid:{cancle:false,pay:true,wait:false,fail:false},
           waited:{cancle:false,pay:false,wait:true,fail:false},
           failed:{cancle:false,pay:false,wait:false,fail:true},
    }
  },
  methods:{
    //
    PostOneOeOrder(index){
       QueryAllorder().then((res)=>{
         if(res.code==200)
         {
           const vo={
             oid:res.data[index].order.id,
             reason:this.form.reason,
             reviewer:this.form.name,
           };
           PostOeOrder(vo).then((res2)=>{
             if(res2.code==200)
             {
               this.dialogFormVisible = false;
               this.$message({message:"",type:"success"});
             }
         })
         }
       })
        
    },
     //
    CancleOnesOrder(index){
       QueryAllorder().then((res)=>{
         if(res.code==200)
         {
           CancleOneOrder(res.data[index]).then((res2)=>{
             if(res2.code==200)
             {
               this.order_list[index].order.status="";
               this.dialogCancleVisible = false;
               this.$message({message:"",type:"success"});
             }
         })
         }
       })
        
    },
    //
    QueryAllOrderAdmin(){
      QueryAllorder().then((res)=>{
        if(res.code==200)
        {
          console.log(res.data);
          this.order_list=res.data;//
          for(var i=0;i<this.order_list.length;i++)
          {
            //
          if(this.order_list[i].order.status==3){this.order_list[i].order.status=""}
          if(this.order_list[i].order.status==2){this.order_list[i].order.status=""}
          if(this.order_list[i].order.status==0){this.order_list[i].order.status=""}
          if(this.order_list[i].order.status==1){this.order_list[i].order.status=""}
            this.order_list[i].order.createTime=parseTime(new Date( this.order_list[i].order.createTime));
            this.order_list[i].order.payAt=parseTime(new Date( this.order_list[i].order.payAt));
            }
        }
       }
      )
    },
    //
    changeOrderCss:function(item){
          if(item.order.status==""){return this.cancled}
          if(item.order.status==""){return this.paid}
          if(item.order.status==""){return this.waited}
          if(item.order.status==""){return this.failed}
      },
      //
    Operation_CancleOrder:function(index){
      this.dialogCancleVisible=true;
      this.form_index=index;
    },
    Operation_CancleOrder_confirm:function(index){
      this.CancleOnesOrder(index);
      // this.testlist[index].status="";
      // this.dialogCancleVisible = false;
    },
    //
    Operation_FailOrder:function(index){
      this.dialogFormVisible=true;
      this.form_index=index;
    },
    Operation_FailOrder_confirm:function(index){
      this.PostOneOeOrder(index);
      //this.testlist[index].statue="";
      //this.dialogFormVisible = false;
    },
    //
    changePad:function(n){
      if(this.activeNames.indexOf(n)==-1){
        this.activeNames.push(n);
      }
       else{
         this.activeNames.pop(n);
       }
    }
  },
  created:function(){
    this.QueryAllOrderAdmin();
  }
}
</script>

<style scoped>
