    height:35px;
    text-align: center;
    display:inline-block;
    border: 1px solid rgb(182, 0, 0);
    color: rgb(218, 15, 0);
    border-radius: 5px;
    background-color: rgb(245, 122, 122);}
    /*  */
    .order-box-item1{
      color:black;
      font-size:18px;
    }
    .PadButton{
    height:35px;
    text-align: center;
    display:inline-block;
    border: 1px solid rgb(68, 68, 68);
    color: rgb(0, 0, 0);
    border-radius: 5px;
    background-color: rgb(238, 238, 238);
    margin-left:60px !important;
    }
    /*  */
